@font-face {
    font-family: 'Roboto';
    src: url('./roboto-regular.woff2') format('woff2'),
        url('./roboto-regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Roboto';
    src: url('./roboto-bold.woff2') format('woff2'),
        url('./roboto-bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}