@import "../../../utils/styles/vars";

.dropdownMenu {
    background-color: var(--bg-color);
    border: 1px solid var(--border-color);
    border-radius: 12px;
    padding: 14px;
    width: fit-content;
    min-width: 150px;

    &__item {
        margin-bottom: 6px;
        cursor: pointer;
        transition: all var(--transition-default);
        padding: 6px 8px;
        border-radius: 6px;
        background-color: transparent;

        &:last-child {
            margin-bottom: 0;
        }

        &:hover {
            background-color: var(--hover-bg-color);
        }

        &_selected {
            background-color: rgba(65, 65, 99, 0.12);
            font-weight: 500;

            &:hover {
                background-color: rgba(65, 65, 99, 0.18);
            }
        }
    }

    &__content {
        display: flex;
        align-items: center;
        gap: 8px;
    }

    &__dot {
        display: inline-block;
        width: 12px;
        height: 12px;
        border-radius: 5px;
        flex-shrink: 0;
    }

    &__label {
        font-size: 12px;
        font-family: "Roboto", sans-serif;
        color: var(--text-color);
        line-height: 1.4;
    }
}